.platform {
  background: url("../img/bgabout.png") center/cover no-repeat;
  padding: 120px 0;

  &__inner {
  }

  &__title {
    font-size: var(--sectionTitle);
    color: var(--black);
    text-align: center;
    margin-bottom: 59px;
  }

  &__content {
  }

  &__left {
    color: var(--black);
  }

  &__p1 {
    font-size: 28px;
    line-height: 120%;
    margin-bottom: 100px;
    max-width: 580px;
  }

  &__p1-span {
    padding: 6px 10px;
    background: var(--pink05);
    border-radius: 50px;
  }

  &__p2 {
    font-size: 28px;
    line-height: 120%;
    margin: 0 auto;
    margin-bottom: 100px;
    max-width: 580px;
  }
  &__p3 {
    font-size: 28px;
    line-height: 120%;
    margin-left: auto;
    margin-bottom: 100px;
    max-width: 580px;
  }

  &__right {
    position: absolute;
    top: -20%;
    right: 0;
    z-index: -1;
  }

  &__right-img {
  }
}
